[data-router-view="about"] {
	background: $light-gray; }

.about-header {
	height: 66vh;
	.container {
		display: flex;
		align-items: center;
		height: 100%; }
	h1 {
		color: $medium-gray;
		@include main;
		line-height: 1em;
		font-size: 3.25vw;
		max-width: 55vw;
		opacity: 0;
		.line-wrapper {
			display: inline-block;
			overflow: hidden;
			line-height: 1.12em; }
		.splitted-line {
			display: inline-block;
			transform: translateY(140%); } } }

.with-margin {
	margin-top: 5.2vh; }

.about-section {
	opacity: 0;
	.container {
		display: flex;
		justify-content: flex-end; }
	&__right {
		width: 70%;
		font-size: 20px;
		line-height: 1.56em;
		display: flex;
		justify-content: space-between; }
	&__left-content {
		width: 40%; }
	&__right-content {
		width: 57%;
		p {
			&::first-line {
				margin-left: 80px;
				text-indent: 67px; } } }
	&__social {
		display: block;
		width: fit-content;
		@include medium;
		&:nth-child(2) {
			margin-top: 5px; } }
	.medium {
		@include medium; } }

.team-section {
	background: white;
	margin-top: 128px;
	margin-bottom: 128px;
	&__wrapper {
		display: flex;
		justify-content: space-between;
		padding-top: 128px;
		padding-bottom: 128px; }
	&__item {
		width: 25%;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		position: relative;
		&::after {
			content: '';
			display: block;
			height: 40px;
			width: 1px;
			background: $medium-gray;
			right: 0;
			top: 25%;
			position: absolute;
			transform: translateY(-50%); }
		&:last-child {
			&::after {
				display: none; } } }
	&__name {
		@include header;
		margin-top: 40px;
		font-size: 17px; }
	&__occupation {
		margin-top: 12px;
		font-size: 13px;
		opacity: 0.64; } }

.about-footer {
	margin-top: 128px;
	padding-bottom: 24px;
	display: inline-block;
	width: 100%;
	.rights {
		width: 70%;
		float: right;
		display: block;
		color: $medium-gray;
		font-size: 20px; } }


@media(max-width: 900px) {
	.about-section {
		.container {
			justify-content: flex-start; }
		&__right {
			width: 100%;
			flex-direction: column; }
		&__left-content {
			width: 100%; }
		&__right-content {
			width: 100%;
			margin-top: 32px; } }
	.about-header h1 {
		font-size: 32px;
		max-width: none; }
	.about-footer {
		.rights {
			width: auto;
			float: none; } } }

@media(max-width: 775px) {
	.team-section {
		&__wrapper {
			flex-direction: column;
			align-items: center;
			padding-top: 64px;
			padding-bottom: 64px; }
		&__item {
			width: 100%;
			margin-top: 112px;
			&::after {
				top: auto;
				bottom: -56px;
				width: 80px;
				height: 1px;
				left: 50%;
				right: auto;
				transform: translate(-50%, -50%); }
			&:first-child {
				margin-top: 0px; } }
		&__name {
			font-size: 20px; } }
	.about-section {
		&__right-content {
			p {
				&::first-line {
					text-indent: 33px; } } } } }
