.navbar {
  position: fixed;
  top: 0;
  padding: 33px 0px;
  left: 0;
  width: 100%;
  z-index: 1002;
  user-select: none;
  transition: background .3s ease;
}

.navbar.filter-open {
  background: white;
  color: #24201E;
}

.navbar__logo {
  font-family: 'Soyuz Grotesk Bold', 'Arial', sans-serif;
  font-weight: 700;
  font-size: 17px;
  letter-spacing: 0.02em;
  line-height: 88%;
}

.navbar .container {
  display: flex;
  align-items: center;
}

.navbar__left {
  display: flex;
  align-items: center;
  width: 36%;
}

.navbar__filter-current {
  margin-left: 33px;
  border: 1px solid #D3D1CF;
  padding: 12px;
  cursor: pointer;
  position: relative;
  transition: border-color .3s ease;
  display: flex;
  justify-content: space-between;
  align-items: center;
  opacity: 0;
  pointer-events: none;
}

.navbar__filter-current:hover {
  border-color: #205A8E;
}

.navbar__filter-current.active {
  opacity: 1;
  pointer-events: auto;
}

.navbar__filter-current svg {
  margin-right: 8px;
}

.navbar__filter-btn {
  margin-left: 14px;
  border: 1px solid #D3D1CF;
  padding: 10px;
  cursor: pointer;
  position: relative;
  transition: border-color .3s ease;
}

.navbar__filter-btn svg {
  display: block;
}

.navbar__filter-btn:hover {
  border-color: #205A8E;
}

.navbar__filter-btn .close {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  pointer-events: none;
  transition: opacity .2s ease;
}

.navbar__filter-btn .open {
  transition: opacity .2s ease;
}

.navbar__filter-btn.open .close {
  opacity: 1;
}

.navbar__filter-btn.open .open {
  opacity: 0;
}

.navbar__link {
  justify-self: center;
  font-size: 0.9vw;
  font-family: 'HelveticaNeueCyr-Bold', 'Arial', sans-serif;
  font-weight: 700;
  transition: color .3s ease;
  align-self: flex-start;
  margin-top: 10px;
}

.navbar__link:hover {
  color: #205A8E;
}

@media (max-width: 1192px) {
  .navbar__left {
    width: auto;
  }
  .navbar__link {
    font-size: 14px;
  }
}

@media (max-width: 1024px) {
  .navbar__links {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: #D3D1CF;
    z-index: 1000;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    text-align: center;
    padding-top: 102px;
  }
  .navbar__link {
    align-self: center;
    font-family: 'Soyuz Grotesk Bold', 'Arial', sans-serif;
    font-weight: 700;
    font-size: 44px;
    line-height: 0.8em;
    width: 100%;
    padding: 24px 74px;
    position: relative;
    transform: translateY(-100%);
  }
  .navbar__link .navbar__line {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 0%;
    height: 1px;
    background: #D3D1CF;
  }
  .navbar__link .navbar__line.last {
    bottom: -12px;
    top: auto;
  }
  .navbar__left.open {
    z-index: 1001;
    position: fixed;
  }
  .navbar .container {
    justify-content: space-between;
    height: 33px;
  }
}

@media (max-width: 1024px) and (max-width: 1024px) {
  .navbar__link {
    font-size: 34px;
  }
}

.burger {
  cursor: pointer;
  display: none;
}

.burger__line {
  width: 40px;
  height: 2px;
  background: #000;
  transition: transform .5s ease, margin-top .5s ease;
}

.burger__line:nth-child(2) {
  margin-top: 4px;
}

@media (max-width: 1024px) {
  .burger {
    display: block;
    padding-top: 6px;
    padding-bottom: 6px;
  }
  .burger.open {
    position: fixed;
    right: 24px;
    z-index: 1000;
  }
  .burger.open .burger__line {
    transform: rotate(225deg);
  }
  .burger.open .burger__line:nth-child(2) {
    transform: rotate(-45deg);
    margin-top: -3px;
  }
}

.desktop-hidden {
  display: none;
}

@media (max-width: 1024px) {
  .desktop-hidden {
    display: block;
  }
}

@media (max-width: 1025px) {
  .tabs {
    position: relative;
  }
  .tabs__nav {
    font-family: 'HelveticaNeueCyr-Bold', 'Arial', sans-serif;
    font-weight: 700;
    display: flex;
    justify-content: space-between;
    border-left: 1px #D3D1CF solid;
    border-right: 1px #D3D1CF solid;
  }
  .tabs__nav-item {
    opacity: .4;
    width: auto;
    padding: 16px 36px;
    width: 33.3%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    border-top: 1px #D3D1CF solid;
    border-bottom: 1px #D3D1CF solid;
    position: relative;
    text-transform: capitalize;
  }
  .tabs__nav-item::before {
    content: '';
    display: block;
    width: 1px;
    height: 50%;
    background: #D3D1CF;
    position: absolute;
    left: -1px;
  }
  .tabs__nav-item.is-active {
    opacity: 1;
    color: #205A8E;
    border-bottom: none;
    border-right: 1px #D3D1CF solid;
    border-left: 1px #D3D1CF solid;
    border-top: none;
  }
  .tabs__nav-item.is-active::before {
    display: none;
  }
  .tabs__nav-item.is-active::after {
    display: none;
  }
  .tabs__nav-item:nth-child(1).is-active {
    border-right: 1px #D3D1CF solid;
    border-left: none;
  }
  .tabs__nav-item:nth-child(3).is-active {
    border-left: 1px #D3D1CF solid;
    border-right: none;
  }
  .tabs__content {
    display: flex;
    justify-content: space-between;
  }
  .tabs__content .tab {
    width: 100%;
    margin-top: 24px;
    position: absolute;
    left: 0;
    display: none;
  }
  .tabs__content .tab.is-active {
    display: block;
  }
  .tabs__item {
    margin-top: 32px;
    cursor: pointer;
    transition: color .3s ease;
  }
  .tabs__item:hover {
    color: #205A8E;
  }
  .tabs__bg {
    width: 33%;
    background: white;
    border-top: 2px #205A8E solid;
    height: 100%;
    position: absolute;
    top: 0;
    z-index: -1;
    transition: transform .3s ease;
    left: 0;
  }
  .only-desktop {
    display: none;
  }
}

