  padding-top: 24px;
  padding-bottom: 24px;
  padding-left: 15px;
  padding-right: 15px;
  width: 16.6%;
  display: flex;
  cursor: pointer;
  justify-content: center;
  border-bottom: 1px solid transparent;
  transition: border-color .3s ease;
}

.interview-randomizer__item::after {
  content: '';
  display: block;
  height: 66%;
  width: 1px;
  background: #D3D1CF;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}

.interview-randomizer__item:last-child::after {
