    top: auto;
    bottom: -56px;
    width: 80px;
    height: 1px;
    left: 50%;
    right: auto;
    transform: translate(-50%, -50%);
  }
  .team-section__item:first-child {
    margin-top: 0px;
  }
  .team-section__name {
    font-size: 20px;
  }
  .about-section__right-content p::first-line {
    text-indent: 33px;
  }
}

*::selection {
  background: #205A8E;
  color: white;
}

.custom {
  scrollbar-width: none;
  scrollbar-height: none;
  -ms-overflow-style: none;
}

.custom::-webkit-scrollbar {
  width: 0px;
  height: 0px;
  background: transparent;
}

.page-rewealer {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1001;
  pointer-events: none;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.page-rewealer svg {
  transform: scale(1.5) rotate(-90deg);
  stroke-dasharray: 3141.276123046875;
  stroke-dashoffset: 3141.276123046875;
  opacity: 0;
  stroke: #f2f2ed;
}

body {
  overflow: auto;
  height: 100vh;
  font-family: 'HelveticaNeueCyr-Roman', 'Arial', sans-serif;
  font-weight: 400;
  color: #24201E;
  transition: color .3s ease, background .3s ease;
  cursor: wait;
  overflow-x: hidden !important;
  -webkit-font-smoothing: antialiased;
}

body.morning {
  background: #fff;
  color: #24201E;
}

body.morning .header__time {
  color: #F6F6F3;
}

body.lunchtime {
  background: #fff;
  color: #24201E;
}

body.lunchtime .header__time {
  color: #205A8E;
}

body.evening {
  background: #F6F6F3;
  color: #24201E;
}

body.evening .header__time {
  color: #fff;
}

body.night {
  background: #24201E;
  color: white;
}

body.night .header__time {
  color: black;
}

body.night .header__clock svg path {
  stroke: #205A8E;
}

body.night .header__clock svg .active path {
  stroke: white;
  stroke-width: 3px;
}

body.night .header__clock svg .active {
  stroke: white;
}

body.night .header__description {
  color: #6A6765;
}

body.night .navbar__filter-btn {
  border-color: #205A8E;
}

body.night .navbar__filter-btn .open path {
  fill: white;
}

body.night .navbar__filter-btn:hover {
  border-color: #F6F6F3;
}

.header {
  width: 100%;
  height: 100vh;
  position: fixed;
  overflow: hidden;
}

.header__clock {
  height: 100%;
  position: fixed;
  z-index: 1;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header__clock svg {
  height: 86%;
  width: 100%;
}

.header__clock svg path {
  opacity: 0;
}

.header__clock .real {
  display: none;
}

.header__clock .real #clock_slider {
  pointer-events: none;
}

.header__clock .real #clock_slider-2 {
  stroke-width: 25px;
  opacity: 0;
}

.header__clock .real .line {
  cursor: pointer;
}

.header__clock .real path {
  opacity: 1;
  transition: stroke-width .3s ease;
}

.header__clock .real.is-visible {
  display: block;
}

.header__clock .real .active path {
  stroke: #205A8E;
  stroke-width: 3px;
}

.header__clock .real .active {
  stroke: #205A8E;
}

.header__time-wrapper {
  position: fixed;
  z-index: 0;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  overflow: hidden;
  height: 9vw;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  width: 100vw;
  user-select: none;
}

.header__time {
  font-size: 18.7vw;
  line-height: 1em;
  transform: translateY(-18%);
  font-family: 'Soyuz Grotesk Bold', 'Arial', sans-serif;
  font-weight: 700;
  display: inline-flex;
}

.header__time span {
  display: inline-block;
  transform: translateY(100%);
}

.header__names {
  position: fixed;
  top: 50%;
  transform: translateY(-50%) translateX(20%) skewX(-25deg);
  text-align: center;
  width: fit-content;
  z-index: 2;
  user-select: none;
  opacity: 0;
  pointer-events: none;
}

.header__names .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  will-change: transform;
}

.header__names .first-strip {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__names .second-strip {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__name-wrapper {
  width: 22vw;
  margin-right: 2.5vw;
  display: none;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  pointer-events: auto;
}

.header__name-wrapper:hover .header__name {
  color: #205A8E;
}

.header__name-wrapper.is-visible {
  display: flex;
}

.header__name-wrapper.default-layout:nth-child(4n+1) {
  margin-top: 33vh;
}

.header__name-wrapper.default-layout:nth-child(4n+2) {
  margin-bottom: 22.3vh;
}

.header__name-wrapper.default-layout:nth-child(4n+3) {
  margin-top: 31vh;
}

.header__name-wrapper.default-layout:nth-child(4n+4) {
  margin-bottom: 20.3vh;
}

.header__name {
  font-family: 'Soyuz Grotesk Bold', 'Arial', sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 3.25vw;
  line-height: 80%;
  transition: color .3s ease;
}

.header__description {
  margin-top: 16px;
  opacity: 0.6;
  font-size: 1vw;
  padding-left: 40px;
  padding-right: 40px;
}

.footer {
  position: fixed;
  bottom: 5%;
  width: 100%;
  z-index: 2;
  font-size: 0.85vw;
  font-family: 'HelveticaNeueCyr-Bold', 'Arial', sans-serif;
  font-weight: 700;
}

.footer .container {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.footer__left svg {
  margin-left: 10px;
}

.footer__right {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.footer__right span {
  letter-spacing: 0.24em;
  text-transform: uppercase;
  font-size: 0.6vw;
  display: inline-block;
  margin-bottom: 3px;
  font-family: 'HelveticaNeueCyr-Roman', 'Arial', sans-serif;
  font-weight: 400;
}

.footer a {
  transition: color .3s ease;
}

.footer a:hover {
  color: #205A8E;
}

.filter {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  height: 100vh;
  background: #F6F6F3;
  align-items: center;
  display: flex;
  opacity: 0;
  pointer-events: none;
  color: #24201E;
  transition: opacity .5s ease;
}

.filter .container {
  display: flex;
  align-items: flex-start;
}

.filter__left {
  width: 36%;
}

.filter__right {
  width: 55%;
}

.filter.open {
  opacity: 1;
  pointer-events: auto;
}

.tabs__nav {
  font-family: 'HelveticaNeueCyr-Bold', 'Arial', sans-serif;
  font-weight: 700;
  display: flex;
  justify-content: space-between;
}

.tabs__nav-item {
  width: 28%;
}

.tabs__content {
  display: flex;
  justify-content: space-between;
}

.tabs__content .tab {
  width: 28%;
  margin-top: 24px;
}

.tabs__item {
  margin-top: 24px;
  cursor: pointer;
  transition: color .3s ease;
}

.tabs__item:hover {
  color: #205A8E;
}

.container {
  padding-left: 3vw;
  padding-right: 3vw;
  width: 100%;
}

h2 {
  font-family: 'Soyuz Grotesk Bold', 'Arial', sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 3.25vw;
  line-height: 0.8em;
  transition: color .3s ease;
}

.link {
  position: relative;
  white-space: nowrap;
}

.link::after {
  content: '';
  display: block;
  background: #205A8E;
  opacity: .64;
  height: 1px;
  width: 100%;
  position: absolute;
  bottom: 4px;
  left: 0;
  transition: opacity .3s ease;
}

.link:hover::after {
  opacity: 1;
}

@media (orientation: portrait) {
  .page-rewealer svg {
    transform: scale(3) rotate(-90deg);
  }
}

