  .header__name-wrapper.default-layout:nth-child(4n+1) {
    margin-top: 25vh;
  }
  .header__name-wrapper.default-layout:nth-child(4n+2) {
    margin-bottom: 16.3vh;
  }
  .header__name-wrapper.default-layout:nth-child(4n+3) {
    margin-top: 23vh;
  }
  .header__name-wrapper.default-layout:nth-child(4n+4) {
    margin-bottom: 14.3vh;
  }
  .header__time-wrapper {
    height: 11vw;
  }
  .header__names {
    top: 60%;
  }
  .navbar__filter-current {
    display: none;
  }
  .container {
    padding-left: 24px;
    padding-right: 24px;
  }
}
