.navbar {
	position: fixed;
	top: 0;
	padding: 33px 0px;
	left: 0;
	width: 100%;
	z-index: 1002;
	user-select: none;
	transition: background .3s ease;
	&.filter-open {
		background: white;
		color: $brown; }
	&__logo {
		@include header;
		font-size: 17px;
		letter-spacing: 0.02em;
		line-height: 88%; }
	.container {
		display: flex;
		align-items: center; }
	&__left {
		display: flex;
		align-items: center;
		width: 36%; }
	&__filter-current {
		margin-left: 33px;
		border: 1px solid $medium-gray;
		padding: 12px;
		cursor: pointer;
		position: relative;
		transition: border-color .3s ease;
		display: flex;
		justify-content: space-between;
		align-items: center;
		opacity: 0;
		pointer-events: none;
		&:hover {
			border-color: $accent-color; }
		&.active {
			opacity: 1;
			pointer-events: auto; }
		svg {
			margin-right: 8px; } }
	&__filter-btn {
		margin-left: 14px;
		border: 1px solid $medium-gray;
		padding: 10px;
		cursor: pointer;
		position: relative;
		transition: border-color .3s ease;
		svg {
			display: block; }
		&:hover {
			border-color: $accent-color; }
		.close {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			opacity: 0;
			pointer-events: none;
			transition: opacity .2s ease; }
		.open {
			transition: opacity .2s ease; }
		&.open {
			.close {
				opacity: 1; }
			.open {
				opacity: 0; } } }

	&__link {
		justify-self: center;
		font-size: 0.9vw;
		@include bold;
		transition: color .3s ease;
		align-self: flex-start;
		margin-top: 10px;
		&:hover {
			color: $accent-color; } }
	@include media("<=1192px") {
		&__left {
			width: auto; }
		&__link {
			font-size: 14px; } }
	@include media("<=1024px") {
		&__links {
			display: none;
			position: fixed;
			top: 0;
			left: 0;
			width: 100%;
			height: 100vh;
			background: $medium-gray;
			z-index: 1000;
			align-items: center;
			justify-content: flex-start;
			flex-direction: column;
			text-align: center;
			padding-top: 102px;
			// padding-left: 74px
 }			// padding-right: 74px
		&__link {
			align-self: center;
			@include header;
			font-size: 44px;
			line-height: 0.8em;
			// border-top: 1px $medium-gray solid
			width: 100%;
			padding: 24px 74px;
			position: relative;
			transform: translateY(-100%);
			.navbar__line {
				display: block;
				position: absolute;
				top: 0;
				left: 0;
				width: 0%;
				height: 1px;
				background: $medium-gray;
				&.last {
					bottom: -12px;
					top: auto; } } }
		&__left {
			&.open {
				z-index: 1001;
				position: fixed; } }
		.container {
			justify-content: space-between;
			height: 33px; }
		@include media("<=1024px") {
			&__link {
				font-size: 34px; } } } }
.burger {
	cursor: pointer;
	display: none;
	&__line {
		width: 40px;
		height: 2px;
		background: #000;
		transition: transform .5s ease, margin-top .5s ease;
		&:nth-child(2) {
			margin-top: 4px; } }
	@include media("<=1024px") {
		display: block;
		padding-top: 6px;
		padding-bottom: 6px;
		&.open {
			position: fixed;
			right: 24px;
			z-index: 1000;
			.burger__line {
				transform: rotate(225deg);
				&:nth-child(2) {
					transform: rotate(-45deg);
					margin-top: -3px; } } } } }

.desktop-hidden {
	display: none;
	@include media("<=1024px") {
		display: block; } }


